<template>
  <div class="container">
    <h1 class="title">Discover Delicious Recipes to Inspire Your Next Meal <img src="data:image/png;base64,"></h1>
    <br>
    <div class="align-items-center mb-3">
    <div>
      <b-form-input v-model="searchTerm" type="search" placeholder="Search terms" style="width: 340px"></b-form-input>
    </div>      
    
    <!-- Display Last Search Term -->
    <div v-if="lastSearchTerm" class="mb-2">
      Last searched term: {{ lastSearchTerm }}
    </div>

    <div class="d-flex align-items-center mb-3">
      <label for="howMany" class="mb-0 mr-2">Number of recipes to show:</label>
      <select id="howMany" class="form-control mr-2" v-model="selectedHowMany">
        <option value="5">5</option>
        <option value="10">10</option>
        <option value="15">15</option>
      </select>
    </div>

    <Filters />
    <b-button @click="search" variant="outline-dark">Search</b-button>

  </div>
    <!-- Display Search Results -->
    <div v-if="showSearchResults && recipes.length > 0" class="mt-4">
        <h3 style="font-weight: bold;">
          Search results:
          <slot></slot>
        </h3>
        <div class="sort-container mb-3">
      <label for="sortBy" class="sort-label mb-0">Sort by:</label>
      <select id="sortBy" v-model="selectedSortBy" class="form-control sort-select">
        <option value="">Select how to sort</option>
        <option value="readyInMinutes">Time (minutes)</option>
        <option value="aggregateLikes">Popularity (likes)</option>
      </select>
      <b-button @click="sortRecipes" variant="outline-dark">Sort</b-button>
    </div>
        <b-row>
          <b-col v-for="r in recipes" :key="r.id">
            <RecipePreview class="recipePreview" :recipe="r" />
          </b-col>
        </b-row>
    </div>
    <div v-else-if="showSearchResults && recipes.length === 0">
      No results found.
    </div>
  </div>
</template>

<script>
import Filters from '../components/Filters';
import { mockGetRecipesPreview } from "../services/recipes.js";
import RecipePreview from "../components/RecipePreview.vue";
export default {
  name: "SearchPage",
  components: {
    Filters,
    RecipePreview
  },
  data() {
    return {
      searchTerm: "",
      lastSearchTerm: "",
      selectedHowMany: '5',
      recipes: [],
      selectedSortBy: '',
      showSearchResults: false // Initially, don't show search results
    };
  },
  methods: {
    async search() {
      // Perform search operation using searchTerm
      // For now, just updating lastSearchTerm and displaying searchResults
      this.lastSearchTerm = this.searchTerm;
      // Simulating search results retrieval
      try {
        const response = await mockGetRecipesPreview(parseInt(this.selectedHowMany));
        const recipes = response.data.recipes;
        this.recipes = []
        this.recipes.push(...recipes);
      } catch (error) {
        console.log(error);
      }
      // Set showSearchResults to true to display search results
      this.showSearchResults = true;
    },
    sortRecipes(recipes, sortBy) {
      if (sortBy === "readyInMinutes"){
        return recipes.sort((a, b) => a.readyInMinutes - b.readyInMinutes);
      }
      return recipes.sort((a, b) => a.aggregateLikes - b.aggregateLikes);
    }
    
  }
};
</script>

<style scoped>
.container {
  max-width: 1200px;
  margin: 100px auto;
  font-family: 'Calibri Light', 'Calibri';
  font-size: large;
  display: flex;
  flex-direction: column;
  justify-content: center; /* Centers vertically */
  align-items: center; /* Centers horizontally */
}
div {
    margin-bottom: 10px; /* Adds space between each filter */
}
label {
  display: inline-block;
  width: 230px; /* Adjust width as needed */
}
select {
  width: 100px; /* Adjust width as needed */
}
.sort-container {
  display: flex;
  align-items: center;
}
.sort-label {
  margin-right: 10px;
  width: auto;
}
.sort-select {
  margin-right: 10px;
  width: auto; /* Adjust as needed */
}

</style>
